
import "./onebike.scss"
import { Head2 } from "../../components/head2/head2";
import {connect} from "react-redux"
import { getOneBike } from "../../actions/actions";
import {http} from "../../untils/getdata"

@connect(
    state=>({
        onebike:state.onebike
    })
)
export class OneBike extends Component{

    componentWillMount(){
        let bikeid = this.props.location.search.split("=")[1]
        this.props.dispatch(getOneBike({url:"/react/onebike?"+"id="+bikeid,cb(){}}))
    }
    addshopcar=()=>{
        let good = this.props.onebike.onebike[0]
        console.log(good)
        let bikeid = this.props.location.search.split("=")[1]
        if(localStorage.usertel){
            http.get("/react/addgood",{params:{
                username:localStorage.usertel,
                detal:good,
                goodid:bikeid,
                count:1
            }}).then(res=>{
                console.log(res)
            })
        }
    }
    render(){
        let bike = this.props.onebike.onebike
        return(
            <div className="ob-box">
                <Head2 history={this.props.history}></Head2>
                {
                    bike&&bike.map((bikes,index)=>{
                        return(
                            <div className="ob-content" key={index}>
                                <img src={bikes.bimg} alt=""/>
                                <p>{bikes.bname} {bikes.btype} {bikes.bmsg}</p>
                                <h3>￥{bikes.bprice}</h3>
                                <h4>全程护航，请放心购买</h4>
                            </div>
                        )
                    })
                }
               
                <FootCar history={this.props.history} toShopcar={this.addshopcar}></FootCar>
                <img src="xq.jpg" alt=""/>
            </div>
        )
    }
}

export class FootCar extends Component{
    goShopcar=()=>{
        this.props.history.push("/shopcar")
    }
    addshop=()=>{
       this.props.toShopcar()
    }
    render(){
        return(
            <div className="footcar">
                <span className="gocar" onClick={this.addshop}>加入购物车</span>
                <span className="buynow" onClick={this.goShopcar}>立即购买</span>
            </div>
        )
    }
}


