
import "./play.scss"

export class Play extends Component{
    constructor(){
        super()
        this.state={
            flag:true
        }
    }
    componentDidMount () {    
        var map = new BMap.Map("mapContainer"); // 创建Map实例
        map.centerAndZoom(new BMap.Point(116.404, 39.915), 11); // 初始化地图,设置中心点坐标和地图级别
        map.addControl(new BMap.MapTypeControl()); //添加地图类型控件
        map.setCurrentCity("武汉"); // 设置地图显示的城市 此项是必须设置的
        map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
        var geolocation = new BMap.Geolocation();
        geolocation.getCurrentPosition(function(r){
            if(this.getStatus() == BMAP_STATUS_SUCCESS){
                var mk = new BMap.Marker(r.point);
                map.addOverlay(mk);
                map.panTo(r.point);
                // alert('您的位置：'+r.point.lng+','+r.point.lat);
            }
            else {
                alert('failed'+this.getStatus());
            }        
        },{enableHighAccuracy: true})
    }
    gostart=()=>{
        if(this.state.flag){
            this.refs.begin.innerHTML="结束"
            this.refs.begin.style.backgroundColor="red"
            this.setState({
                flag:false
            })
        }else{
            this.refs.begin.innerHTML="开始"
            this.refs.begin.style.backgroundColor="#189adb"
            this.setState({
                flag:true
            })
        }
        
    }
    render(){
        return(
            <div className="p-box">
                <div id="mapContainer" style={{width:"100%",height:200,marginTop:15}}></div>
                <div className="p-number">
                    <ol>
                        <li>
                            <p>00.00</p>
                            <span>里程(KM)</span>
                        </li>
                        <li>
                            <p>00:00</p>
                            <span>运动时间</span>
                        </li>
                        <li>
                            <p>00.00</p>
                            <span>均速(KM/h)</span>
                        </li>
                    </ol>
                </div>
                <div className="p-click" onClick={this.gostart} ref="begin">
                    开始
                </div>
            </div>
        )
    }
}


