

import {NavLink} from "react-router-dom"
import "./mine.scss"
import { Head2 } from "../../components/head2/head2";
export class Mine extends Component{
    constructor(){
        super()
        this.state={
            ico:"icon-shezhi",
            usertel:localStorage.usertel
        }
    }
    exitlogin=()=>{
        localStorage.removeItem("usertel")
        this.setState({
            usertel:localStorage.usertel
        })
    }
    render(){
        let usertel=this.state.usertel
        return(
            <div className="m-box">
                <Head2 history={this.props.history}></Head2>
                <div className="m-top" style={{display:usertel?"block":"none"}}>
                    <img src="http://39.97.167.207/images/head.png" alt=""/>
                    <p>{usertel}</p>
                    {/* className={"iconfont "+this.state.ico} */}
                    <i onClick={this.exitlogin}>退出登录</i>
                </div>
                <div className="m-top2" style={{display:!usertel?"block":"none"}}>
                    <img src="http://39.97.167.207/images/head.png" alt=""/>
                    <p>您还没有登录哦</p>
                    <NavLink to="/login"><i>登录</i></NavLink>
                </div>
            </div>
        )
    }
}


