import "./register.scss"
import { Head2 } from "../../components/head2/head2";
import {http} from "../../untils/getdata"
import { Toast } from 'antd-mobile';


export class Register extends Component{
    constructor(){
        super()
        this.state={
            flag1:false,
            flag2:false
        }
    }

    regtel=()=>{
        let telreg=/^[1][3,4,5,7,8][0-9]{9}$/;
        let telnum = this.refs.tel.value;
        if(telreg.test(telnum)){
            this.refs.tel.style.color="green"
            this.setState({
                flag1:true
            })
        }else{
            this.refs.tel.style.color="red"
            this.setState({
                flag1:false
            })
        }
    }
    regpwd=()=>{
        let pwdinp = this.refs.pwd.value;
        let pwdinp2 = this.refs.pwd2.value;
        if(pwdinp!=""&&pwdinp==pwdinp2){
            this.setState({
                flag2:true
            })
            if(this.state.flag1){
                this.refs.btn.style.backgroundColor="green"
            }else{
                this.refs.btn.style.backgroundColor="#ddd"
            }
        }else{
            this.setState({
                flag2:false
            })
            this.refs.btn.style.backgroundColor="#ddd"
        }
        
    }
   
    zhuce=()=>{  
        var telnum = this.refs.tel.value;  
        var pwdinp = this.refs.pwd.value;     
        if(this.state.flag1&&this.state.flag2){
            http.post("/react/reg",{params:{tele:telnum,pwds:pwdinp}}).then(res=>{
                console.log(res)
                if(res.data.code==200){
                    this.props.history.push("/login")
                }else{
                    Toast.info(res.data.msg,1)
                }
            })
        }
    }
    render(){
        return(
            <div className="r-box">
                <Head2 history={this.props.history}></Head2>
                <div className="r-img">
                    <img src="http://39.97.167.207/images/login.jpg" alt=""/>
                </div>
                <div className="r-inp">
                    <p className="r-tel">手机号: <input type="tel" placeholder="请输入手机号" ref="tel" onBlur={this.regtel}/></p>
                    <p className="r-yzm">密　码: <input type="password" placeholder="请输入密码" ref="pwd"/></p>
                    <p className="r-pwd">请确认: <input type="password" placeholder="请确认密码" ref="pwd2" onInput={this.regpwd}/></p>
                    <button onClick={this.zhuce} ref="btn">注 册</button>
                </div>
            </div>
        )
    }
}


