

import axios from "axios"

// axios.defaults.baseURL = "http://localhost:1810/"  //开发地址
axios.defaults.baseURL = "http://39.97.167.207:1810/"  //生产地址地址

export const http = axios;


//原生Ajax

export const $http = (function(){
    const ajax = function(url,type,callback){
        var xhr = new XMLHttpRequest();
        xhr.open(type,url,true);
        xhr.send();
        xhr.onreadystatechange = function(){
            if(xhr.readyState==4&&xhr.status==200){
                callback(JSON.parse(xhr.responseText))
            }
        }
    }
    return {request:ajax}
})();


exports.getQuery  = function(search){
    var url = require("url")
    return url.parse(search,true).query;
}


