
import "./buy.scss"
import { Carousel, WingBlank } from 'antd-mobile';
import {Link} from "react-router-dom"
import {connect} from "react-redux"
import { Head2 } from "../../components/head2/head2";
import { getBike } from "../../actions/actions";
import { Toast } from 'antd-mobile';


export class Buy extends Component{
    state = {
        data: ['sichou.jpg', 'hainan.jpg', 'chuanzang.jpg'],
        imgHeight: 176,
    }
    render(){
        return(
            <div className="b-box">
                <Head2 history={this.props.history}></Head2>
                <WingBlank>
                    <Carousel
                    autoplay={true}
                    infinite
                    >
                    {this.state.data.map((imgs,val) => (
                        <Link
                        key={val}
                        to={"/app/book"}
                        style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                        >
                        <img
                            src={"http://39.97.167.207/images/"+imgs}
                            alt=""
                            style={{ width: '100%', verticalAlign: 'top' }}
                            onLoad={() => {
                            // fire window resize event to change height
                            window.dispatchEvent(new Event('resize'));
                            this.setState({ imgHeight: 'auto' });
                            }}
                        />
                        </Link>
                    ))}
                    </Carousel>
                </WingBlank>
                <BikeList></BikeList>
                <div className="b-bot"></div>
            </div>
        )
    }
}


@connect(
    state=>({
        bike:state.bikelist
    })
)
export class BikeList extends Component{
    componentWillMount(){
        Toast.loading("加载中...",1000000)
        this.props.dispatch(getBike({url:"/react/bikelist",cb:()=>{
            Toast.hide();
            Toast.info("加载成功",1)
        }}))
    }
    render(){
        let{bikelist}=this.props.bike
        return(
            <ol className="b-bikelist">
                {
                    bikelist.map((bikes,index)=>{
                        return(
                            <li className="b-bikeone" key={index}>
                                <Link to={"/onebike?"+"id="+bikes._id}>
                                    <img src={bikes.bimg} alt=""/>
                                    <p>{bikes.bname} {bikes.btype} {bikes.bmsg}</p>
                                    <h3>￥{bikes.bprice}</h3>
                                </Link>
                            </li>
                        )
                    })
                }
                
                
            </ol>
        )
    }
}


