

import {Route,Switch,Redirect} from "react-router-dom"
// import { Home } from "../home/home";
import { Mine } from "../mine/mine";
import {Foot} from "../../components/foot/foots"
import { Find } from "../find/find";
import { connect } from "react-redux";
import { Buy } from "../buy/buy";
import { Play } from "../play/play";
import {Book} from "../book/book";

@connect(
    (state)=>{
        return{
            footers:state.foots
        }
    }
)
export class App extends Component{
    render(){
        const {footers} = this.props
        return(
            <div>
                <Switch>
                    {/* <Route path="/app/home" component={Home}></Route> */}
                    <Route path="/app/mine" component={Mine}></Route>
                    <Route path="/app/play" component={Play}></Route>
                    <Route path="/app/buy" component={Buy}></Route>
                    <Route path="/app/book" component={Book}></Route>
                    <Route path="/app/find" component={Find}></Route>
                    <Route render={()=><Redirect to="/app/mine"/>}></Route>
                </Switch>
                <Foot footer={footers} history={this.props.history}></Foot>
            </div>
        )
    }
}


