

//整个项目的 主组件 路由

import {HashRouter as Router,Switch,Route,Redirect} from "react-router-dom";
import { Guide } from "./guide";
import { App } from "./app/app";
import { Login } from "./login/login";
import { Road } from "./road/road";
import { OneBike } from "./bike/onebike";
import { Register } from "./register/register";
import { ShopCar } from "./shopcar/shopcar";




export class IndexView extends Component{
    render(){
        return(
            <div style={{width:"100%",height:"100%"}}>
                <Router basename="/">
                    <Switch>
                        {/* <Route path="/" render={()=><Redirect to="/guide"/>}></Route> */}
                        <Route path="/index" component={Guide} exact></Route>
                        <Route path="/app" component={App}></Route>
                        <Route path="/login" component={Login}></Route>
                        <Route path="/road" component={Road}></Route>
                        <Route path="/onebike" component={OneBike}></Route>
                        <Route path="/register" component={Register}></Route>
                        <Route path="/shopcar" component={ShopCar}></Route>
                        <Route component={Guide}></Route>  
                    </Switch>
                </Router>
            </div>
        )
    }
}


