

let footer = {
    foots:[
        {txt:"我的",path:"/app/mine",name:"mine",icon:"icon-wode1"},
        {txt:"发现",path:"/app/find",name:"find",icon:"icon-faxian"},
        {txt:"运动",path:"/app/play",name:"play",icon:"icon-cycleqiche"},               
        {txt:"路书",path:"/app/book",name:"book",icon:"icon-book_icon"},
        {txt:"好货",path:"/app/buy",name:"buy",icon:"icon-shousuo"},
    ] 
}

export default (state=footer.foots,action)=>{
    switch(action.type){

        default:
        return state;
        break;
    }
}



