


import {http} from "../untils/getdata"

export const  getLushu =({url,cb})=>{
    return http.get(url).then(res=>{
        cb()
        return{
            type:"LUSHU",
            lushu:res.data.result
        }
    })
}

export const getOneLushu =({url,cb})=>{
    return http.get(url).then(res=>{
        cb(res.data.result[0])
        return{
            type:"ONELUSHU",
            onelushu:res.data.result

        }
    })
}

export const getBike = ({url,cb})=>{
    return http.get(url).then(res=>{
        cb()
        return{
            type:"GETBIKE",
            bikelist:res.data.result
        }
    })
}

export const getOneBike = ({url,cb})=>{
    return http.get(url).then(res=>{
        cb()
        return{
            type:"ONEBIKE",
            onebike:res.data.result
        }
    })
}


