
import "./book.scss"
import { Head2 } from "../../components/head2/head2";

import {connect} from "react-redux"
import { getLushu } from "../../actions/actions";
import {NavLink} from "react-router-dom"
import { Toast } from 'antd-mobile';

@connect(
    state=>({
        books:state.books
    })
)
export class Book extends Component{
    componentWillMount(){
        Toast.loading("加载中...",1000000)
        this.props.dispatch(getLushu({url:"/react/book",cb:()=>{
            Toast.hide();
            Toast.info("加载成功",1)
        }}))
    }
    render(){
        let{books}=this.props
        return(
            <div className="b-box">
                <Head2 history={this.props.history}></Head2>
                {
                    books.lushu.map((book,index)=>{
                        return(
                            <div key={index} className="b-list">
                                <NavLink to={"/road?"+"id="+book._id}>
                                    <img src={book.lineimg} alt=""/>
                                    <h3>{book.linename}</h3>
                                </NavLink>
                            </div>
                        )
                    })
                }
            </div>
        )
    }
}


