

import "./head.scss"
import { NavBar, Icon } from 'antd-mobile';

export class Heads extends Component{
        constructor(){
            super()
            this.state={
                flag:false,
            }
        }
    
        changHeight=()=>{
            
            if(this.state.flag){
                this.setState({
                    flag:false
                })
                this.refs.one.style.height = 0;
                
            }else{
                this.setState({
                    flag:true
                })
                this.refs.one.style.height = "100%";  
            }
            
        }

        goindex=()=>{
            this.props.history.push("/app/buy")   
        }
        gobook=()=>{
            this.props.history.push("/app/book")   
        }
        
    render(){  
        return(
            <div>
                <NavBar
                    mode="dark"
                    icon={<Icon type="ellipsis" />}
                    onLeftClick={this.changHeight}        
                >行者天下</NavBar>
                <div className="h-guide" ref="one">
                    <p onClick={this.goindex}>行者</p>
                    <p onClick={this.gobook}>路书</p>
                    <p>骑行吧</p>
                </div>
            </div>
        )
    }
}


