

import "./index.scss"
// import bjurl from "http://39.97.167.207/images/top_banner.jpg"
import { Heads } from "../../components/head/head";
var bj = {
    width:"100%",
    height:"300px",
    background:"url('http://39.97.167.207/images/top_banner.jpg') no-repeat",
    backgroundSize:"cover"
}


export class Guide extends Component{
    
    render(){
        return(
            <div className="g-box">
                <Heads {...this.props}></Heads>
                <GuideTop></GuideTop>
                <GuideSide {...this.props}></GuideSide>
                <video src="https://oibjq9rlf.qnssl.com/video-Xingzhe_100s_FINAL_720P.mp4" controls poster="https://cdn.bi-ci.com/vue_static/images/video_poster.jpg"></video>
            </div>
        )
    }
}

export class GuideTop extends Component{
    constructor(){
        super()
        this.state={
            num:1345678
        }
    }
    componentWillMount(){
        setInterval(()=>{
            this.state.num+=6
            this.setState({
                num: this.state.num
            })
        },800)
    }
       
    render(){
        return(
            <div className="g-boxtop" style={bj}>
                <h3>行者，梦想和远方</h3>
                <p>当前行者用户骑行总里程</p>
                <h2>{this.state.num}公里</h2>
            </div>
        )
    }
}


export class GuideSide extends Component{
    // state={
    //     imgurl1:require("../../../assets/images/home-side-lushu.jpg"),
    //     imgurl2:require("../../../assets/images/home-side-shebei.jpg"),
    // }
    changOpacity=()=>{
        let {history} = this.props;
        this.refs.ls.style.opacity = 1;
        this.refs.sb.style.opacity = 0;
        setTimeout(function(){
            history.push("/app")
        },2000)
    }
    changOpacity2=()=>{
        this.refs.ls.style.opacity = 0;
        this.refs.sb.style.opacity = 1;
    }
    render(){
        return(
            <div className="g-side">
                <div className="g-side-ls" onClick={this.changOpacity}>
                    <div ref="ls">
                        <h2>行者路书</h2>
                        <p>提前规划制作行者路书，跟随路书让你的出行安全无忧，再也不担心迷路！还能发现更多精彩路书</p>
                    </div>
                    <img src="http://39.97.167.207/images/home-side-lushu.jpg" alt=""/>
                </div>
                <div className="g-side-sb" onClick={this.changOpacity2}>
                    <div ref="sb">
                        <h2>智能设备</h2>
                        <p>连接你的运动智能设备，实时监控你的速度／心率／踏频／功率等运动数据，随时随地调整身体状态</p>
                    </div>
                    <img src="http://39.97.167.207/images/home-side-shebei.jpg" alt=""/>
                </div>
            </div>
        )
    }
}


