
import "./road.scss"
import { Head2 } from "../../components/head2/head2";
import { connect } from "react-redux";
import { getOneLushu } from "../../actions/actions";
import { Toast } from 'antd-mobile';

@connect(
    state=>({
        onelushu:state.onelushu
    })
)
export class Road extends Component{ 
    constructor(){
        super()
        this.state={
            flag:true,
        }
    }
     
    changeHeight=()=>{
        if(this.state.flag){
            this.refs.ones.style.height="auto"
            this.refs.onep.innerHTML="收起"
            this.setState({
                flag:false
            })
        }else{
            this.refs.ones.style.height="56px"
            this.refs.onep.innerHTML="更多"
            this.setState({
                flag:true
            })
        }       
    }
    componentWillMount(){
        Toast.loading("加载中...",1000000)
        let roadid = this.props.location.search.split("=")[1]
        this.props.dispatch(getOneLushu({url:"/react/onebook?"+"id="+roadid,cb:()=>{
            Toast.hide();
            Toast.info("加载成功",1)
        }}))
    }
    componentDidMount () {
        
        var map = new BMap.Map("mapContainer"); // 创建Map实例
        map.centerAndZoom(new BMap.Point(116.404, 39.915), 11); // 初始化地图,设置中心点坐标和地图级别
        map.addControl(new BMap.MapTypeControl()); //添加地图类型控件
        map.setCurrentCity("武汉"); // 设置地图显示的城市 此项是必须设置的
        map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
        // var geolocation = new BMap.Geolocation();
        // geolocation.getCurrentPosition(function(r){
        //     if(this.getStatus() == BMAP_STATUS_SUCCESS){
        //         var mk = new BMap.Marker(r.point);
        //         map.addOverlay(mk);
        //         map.panTo(r.point);
        //         // alert('您的位置：'+r.point.lng+','+r.point.lat);
        //     }
        //     else {
        //         alert('failed'+this.getStatus());
        //     }        
        // },{enableHighAccuracy: true})
        var walking = new BMap.WalkingRoute(map, {renderOptions:{map: map, autoViewport: true}});
        walking.search("拉萨","成都");
    }
    render(){

       let onelushu=this.props.onelushu.onelushu  
        return(
            <div className="h-box">
                <div className="h-sbox">
                    <Head2 history={this.props.history}></Head2>
                    {
                        onelushu.map((alushu,index)=>{
                            return(
                                <div key={index}>
                                    <img src={alushu.lineimg} alt=""/>
                                    <div style={{height:"56px",overflow:"hidden",marginTop:15,boxSizing:"border-box",padding:"0 10px"}} ref="ones">{alushu.linemsg}</div>
                                    <p style={{fontSize:14,textAlign:"center",color:"#108ee9"}} onClick={this.changeHeight} ref="onep">更多</p>
                                </div>
                            )
                        })
                    }
                    <div id="mapContainer" style={{width:"100%",height:200,marginTop:15}}></div>          
                </div>
            </div>
        )
    }
}


