


import {combineReducers} from "redux"
import foots from "./foots"
import lushu from "./lushu";
import onelushu from "./onelushu";
import bikes from "./bikes";
import onebike from "./onebike";

const reducers = combineReducers({
    foots:foots,
    books:lushu,
    onelushu:onelushu,
    bikelist:bikes,
    onebike:onebike
})
export default reducers;


