


import {observable, useStrict, action} from "mobx";
import {http} from "../untils/getdata"

class ShopcarInfo{
    @observable carList = [];

    @action getCarlist = username =>{
        http.get("/react/getCarList",{
            params:{username}
        }).then(res=>{
            console.log(res)
            this.carList=res.data.result
        })
    }
}


export default new ShopcarInfo()


