
import { List, Stepper } from 'antd-mobile';
import "./shopcar.scss";
import { Head2 } from "../../components/head2/head2";
import {observer} from "mobx-react"
import shopcarInfo from "../../mobx/shoppingcar"
console.log(shopcarInfo)
@observer
export class ShopCar extends Component{
    state={
        num:10
    }
    componentWillMount(){
        shopcarInfo.getCarlist(localStorage.usertel)
    }
    componentDidMount(){
        console.log(shopcarInfo)
    }
    render(){
        return(
            <div className="sc-box">
                <Head2 history={this.props.history}></Head2>
                <div className="sc-content">
                {
                    shopcarInfo.carList&&shopcarInfo.carList.map((item,index)=>(
                        <ol key={index}>
                            <li className="fli"><input type="checkbox"/></li>
                            <li className="sli"><img src={JSON.parse(item.detal).bimg} alt=""/></li>
                            <li className="tli"><p>{JSON.parse(item.detal).bname}{JSON.parse(item.detal).btype}</p></li>
                            <li className="lli"><Stepper
                                style={{ width: '80%', minWidth: '100px' }}
                                showNumber
                                max={10}
                                min={1}
                                value={item.count}
                                onChange={this.onChange}
                                />
                            </li>
                        </ol>
                        )
                    )
                }
                </div>
                <div className="sc-bot">
                    <span className="fspan">全选&emsp; <input type="checkbox"/></span>
                    <p>总计:￥5678</p>
                    <span className="lspan">结 算</span>
                </div>
            </div>
        )
    }
}


