

import { NavBar, Icon } from 'antd-mobile';


export class Head2 extends Component{
    render(){
        let {history} = this.props;
        return(
            <div>
                <NavBar
                    mode="dark"
                    icon={<Icon type="left" />}
                    onLeftClick={()=>history.go(-1)}        
                >行者天下</NavBar>
            </div>
        )
    }
}


