

import "./login.scss"
import { Head2 } from "../../components/head2/head2";
import {Link} from "react-router-dom"
import {http} from "../../untils/getdata"
import { Toast } from 'antd-mobile';


export class Login extends Component{
    constructor(){
        super()
        this.state={
            flag1:false,
            flag2:false
        }
    }
    logintel=()=>{
        var telinp = this.refs.logtel.value
        var telreg=/^[1][3,4,5,7,8][0-9]{9}$/;
        if(telreg.test(telinp)){
            this.setState({
                flag1:true
            })
        }else{
            this.setState({
                flag1:false
            })
        }
    }
    loginpwd=()=>{
        var pwdinp = this.refs.logpwd.value
        if(pwdinp!=""){
            this.setState({
                flag2:true
            })
            if(this.state.flag1&&this.state.flag2){
                this.refs.logbtn.style.backgroundColor="green"
            }else{
                this.refs.logbtn.style.backgroundColor="#ddd"
            }
        }else{
            this.setState({
                flag2:false
            }) 
        }
    }
    tologin=()=>{
        var telinp = this.refs.logtel.value
        var pwdinp = this.refs.logpwd.value
        http.post("/react/login",{params:{tele:telinp,pwds:pwdinp}}).then(res=>{
            if(res.data.code==200){
                localStorage.usertel = telinp
                this.props.history.push("/app/mine")
            }else{
                Toast.info(res.data.msg,1)
            }
        })
    }
    render(){
        return(
            <div className="l-box">
                <Head2 history={this.props.history}></Head2>
                <div className="l-img">
                    <img src="http://39.97.167.207/images/login.jpg" alt=""/>
                </div>
                <div className="l-inp">
                    <p className="l-tel">手机号: <input type="tel" onChange={this.logintel} ref="logtel"/></p>
                    <p className="l-yzm">密　码: <input type="text" onChange={this.loginpwd} ref="logpwd"/> <span>获取验证码</span></p>
                    <button onClick={this.tologin} ref="logbtn">登 录</button>
                </div>
                <h3><Link to={"/register"}> 没有账号,注册新账号</Link></h3>
            </div>
        )
    }
}


